// src/lib/auth/options.js
import { getServerSession } from "next-auth"

const DEFAULT_USER = {
  sub: "demo-user",
  name: "Demo User",
  email: "demo.user@example.com",
  preferred_username: "demo",
  picture: "https://avatars.githubusercontent.com/u/9919?v=4",
}

function resolveIssuer() {
  const explicitIssuer = process.env.AUTH_OIDC_ISSUER?.trim()
  if (explicitIssuer) return explicitIssuer
  const baseUrl = process.env.NEXTAUTH_URL?.replace(/\/$/, "")
  if (!baseUrl) return "http://localhost:3000/api/mock-oidc"
  return `${baseUrl}/api/mock-oidc`
}

export const authOptions = {
  providers: [
    {
      id: "oidc",
      name: "Enterprise SSO",
      type: "oauth",
      wellKnown: `${resolveIssuer()}/.well-known/openid-configuration`,
      clientId: process.env.AUTH_OIDC_ID ?? "demo-client",
      clientSecret: process.env.AUTH_OIDC_SECRET ?? "demo-secret",
      checks: ["pkce", "state"],
      authorization: {
        params: { scope: "openid profile email" },
      },
      profile(profile) {
        const source = profile && typeof profile === "object" ? profile : DEFAULT_USER
        return {
          id: source.sub || DEFAULT_USER.sub,
          name: source.name || source.preferred_username || DEFAULT_USER.name,
          email: source.email || DEFAULT_USER.email,
          image: source.picture || null,
        }
      },
    },
  ],
  session: {
    strategy: "jwt",
  },
  pages: {
    signIn: "/login",
  },
  callbacks: {
    async jwt({ token, profile }) {
      if (profile) {
        token.name = profile.name || token.name
        token.email = profile.email || token.email
        token.picture = profile.picture || token.picture
      }
      return token
    },
    async session({ session, token }) {
      if (session.user) {
        session.user.name = token.name || session.user.name
        session.user.email = token.email || session.user.email
        session.user.image = token.picture || session.user.image
      }
      return session
    },
  },
}

export function getServerAuthSession() {
  return getServerSession(authOptions)
}
